/* socialLinks.css */

/* Container for social icons */
.social-icons {
    
    display: flex;
    
    justify-content: right;
    gap: 12px;
    margin-right: 165px;
}

/* Social Icon Styles */
.social-icon {
    color: #333;
    /* Default outline color */
    transition: transform 0.3s ease, color 0.3s ease;
}

.social-icon:hover {
    transform: scale(1.1);
}

.social-icon svg {
    width: 30px;
    height: 30px;
}

/* SVG Path Styles */
.social-icon path {
    fill: none;
    stroke: currentColor;
    transition: all 0.3s ease;
}

/* Hover Effects: Fill with Gradient and Remove Stroke */
.social-icon.gmail:hover path {
    fill: url(#gmailGradient);
    stroke: none;
}

.social-icon.github:hover path {
    fill: url(#githubGradient);
    stroke: none;
}

.social-icon.linkedin:hover path {
    fill: url(#linkedinGradient);
    stroke: none;
}

.social-icon.youtube:hover path {
    fill: url(#youtubeGradient);
    stroke: none;
}

/* Optional: Change color on hover if needed */
.social-icon.gmail:hover {
    color: #EA4335;
}

.social-icon.github:hover {
    color: purple;
}

.social-icon.linkedin:hover {
    color: #00a0dc;
}

.social-icon.youtube:hover {
    color: #C80000;
}

/* FORM SECTION */

/* Form Container Styles */
.form-container {
    max-height: 0;
    overflow: hidden;
    
    transition: max-height 0.5s ease-out, opacity 0.5s ease-out;
    opacity: 0;
    
}

.form-container.show {
    max-height: 400px;
    /* Adjust based on your form's height */
    opacity: 1;
    
}

/* Form Styles */
.FormContact {
    background-color: transparent;
    /* Transparent background */
    margin: 237px auto;
    right:340px;
    padding: 20px;
    max-width: 500px;
    position: fixed;
    /* For positioning the close button */
    display: flex;
    flex-direction: column;
    gap: 20px;
    
    border: red;
    /* Ensure no border is applied */
    box-shadow: red;
    /* Remove any shadow */
    /* Slight shadow for raised effect */
}

/* Input and Textarea Styles */
.Input,
.Textarea {
    width: 70%;
    color:white;
    padding: 12px;
    border: none;
    border-radius: 8px;
    background-color: transparent;
    background-image: linear-gradient(45deg, rgb(0, 0, 0), rgb(45, 42, 42), rgb(43, 41, 41));
    transition: all 0.3s ease;
    font-size: 16px;
}

/* HERE FOR SEND TO FRONT TEXT*/
.Input:focus,
.Textarea:focus {
    outline: none;
    box-shadow:
        0 4px 15px rgba(0, 0, 0, 0.15),
        0 0 0 2px rgba(0, 0, 0, 0.1);
    transform: translateY(-2px);
}

.Textarea {
    height: 120px;
    resize: vertical;
    
    background-image:transparent;
}
.Input:hover ,
.Textarea:hover{
    
    
    background-image: black;

    box-shadow: none;
    transform: translateY(-2px);
    transition: all 0.3s ease;
}

/* Button Styles */
.Button {
    align-self: flex-start;
    padding: 10px 22px;
    margin-left: 110px;
    background-color: linear-gradient(45deg, rgb(218, 26, 26), rgb(203, 87, 87), rgb(248, 35, 6));
    
    margin-top: 30px;
    border-radius: 10px;
    cursor: pointer;
    transition: all 0.3s ease;
    box-shadow: red;
    font-size: 20px;
}

.Button:hover {
    background-image: linear-gradient(45deg, rgb(218, 26, 26), rgb(203, 87, 87), rgb(248, 35, 6));
    
    box-shadow: none;
    transform: translateY(-2px);
    transition: all 0.3s ease;
}